li {
  list-style-type: none;
}
table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
          }
          
td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
          }
          
tr:nth-child(even) {
  background-color:#b1acac;
          }

table {
          display: inline-block;
          vertical-align: top;
        }